body{
background-color: black;
}

.App{
  display: flex;
  align-items: center;
  justify-content: center;
}

span{
  background-color: #888;
  width: 400px;
  height: 550px;
  margin-top: 25px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  flex-direction: column;
}

h1{
  line-height: 10px;
  padding-top: 12px;
  padding-bottom: 0;
  text-shadow: 6px 7px 10px #555555;
  color: rgba(57, 65, 73, 0.61);
}


h3{
  font-size: 20px;
  text-shadow: 6px 7px 10px #555555;
}

button{
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
  background-color: #111;
  border-radius: 6px;
  border: none;
  color: white;
  padding: 10px;
  box-shadow: 6px 7px 6px #555555;
}

span h2{
  padding: 12px;
  text-align: center;
  text-shadow: 6px 7px 6px #555555;
}

input{
  margin: 0;
  padding: 10px;
  border-radius: 6px;
  border: none;
  outline: none;
  width: 280px;
  height: 40px;
  font-size: 20px;
  box-shadow: 6px 7px 6px #555555;
}





